@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;600;700&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Work Sans', sans-serif;
}
/* #root{

} */
#root,html,body{
  display: flex;  
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row;
  
  
}
.container{
  /* background-color: red; */
  /* height: 100vh; */
  width: 100vw;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-wrap: wrap;
 gap: 20px;
 padding:15px 20px;
}
.Card-Container{
background-color: #2e343f;
border-radius: 5px;
box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
color: #dfe6e9;
padding-top: 10px;
width: 350px;
max-width: 100%;
text-align: center;
position: relative;
margin-bottom: 5px;
margin-top: 5px;
/* background-color: red; */
}
.img{
  margin-top: 15px;
  width: 100px;
  border: 3px solid white;
  padding: 5px;
  border-radius: 50%;
  object-fit: cover;
  height: 100px;
}
.pro{
  position: absolute;
  top: 30px;
  left: 30px;
  color: black;
  padding: 2px 6px;
  border-radius: 5px;
}
.online{
  background-color: #C4E538;
 
}
.offline{
  
  background-color: #FFC312;
}
h3{
  margin: 5px 0;
  font-weight: 200;
}
p{
  margin: 8px 0;
}
.buttons{
  display: flex;
  justify-content: center;
  gap: 10px;
  padding: 10px 20px;
  /* background-color: red; */
}
.primary{
  padding: 10px 25px;
  background-color: #0652DD;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  padding: 10px 35px;
  transition: .1s;
}
.outline{
  /* background: transparent; */
  border: 1px solid #0652DD;
  
}
.primary:hover{
  background: transparent;
  border: 1px solid #0652DD;
  transition: .1s;
}
h6{
  font-size: 12px;
  font-weight: 100;
  text-align: left;
  padding-left:10px ;
  margin-bottom: 5px;
  border-bottom: 1px solid #595180;
  padding-bottom: 5px;
}
.skills{
  text-align: left;
  padding: 15px;
  margin-top: 5px;
  /* background-color: red; */
}
.skills ul{
  padding-top: 5px;
  list-style: none;
  /* background-color: red; */
}
.skills ul li{
  border: 1px solid #595180;
  display: inline-block;
  border-radius: 2px;
  padding: 7px;
  margin: 0 15px 10px 0;
  font-size: 12px;
  
}
