@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;600;700&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Work Sans', sans-serif;
}


.qr-code-image{
    margin-top: 15px;
    width: 200px;

    border: 3px solid white;
    padding: 5px;
    /* border-radius: 50%; */
    object-fit: cover;
    height: 200px;
}
.app-container{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.app-container h1{
    font-weight: 600;
    font-size: 18px;
    color: #3498db;
    border-bottom: 1px solid #ccc;
    padding: 1rem 0rem;
}
.input-label{
    display: block;
    margin-bottom: .5rem;
    color: #3498db;
    font-size: 1.1rem;
    font-weight: 500;
}
input[type="text"]{
    padding: 1.2rem;
    margin-bottom: .7rem;
    width: 100%;
    font-size: 16px;
    border: 2px solid #3498db;
    border-radius: 5px;
}
button{
    padding: 1.5rem 2rem;
    font-size: 18px;
    color: #fff;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;
}
.generate-button{
    background-color: #3498db;
}
.generate-button:disabled{
    background-color: #bdc3c7;
    cursor: not-allowed;
}
.download-button{
    background-color: #27ae60;
    margin-left: 1rem;
}
.download-button:hover{
    background-color: #219a52;
}
.footer{
    margin-top: 1rem;
    text-decoration: none;
    color: #3498db;
}